import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { getOrders } from './../../slices/adminOrdersSlice';
import Loading from './../../components/loading';
import Pagination from './../../components/pagination';
import './orders.scss';
import { useEffect } from 'react';

export default function Orders() {
  const dispatch = useDispatch();
  const { user } = useSelector((store) => store.user);
  const { orders, isLoading, pagination } = useSelector((store) => store.adminOrders);
  const [data, setData] = useState({});
  const [show, setShow] = useState(false);
  const [fileUploading, setFileUploading] = useState('');
  const [nowPage, setNowPage] = useState(1);

  const handleToggle = (bool) => {
    setShow(bool);
  };

  const passData = (data) => {
    setData(data);
  }

  useEffect(() => {
    dispatch(getOrders(nowPage));
  }, [])

  useEffect(() => {
    dispatch(getOrders(nowPage));
  }, [nowPage])

  if(isLoading) {
    return <Loading />
  }

  return (
    <>
        <table className="table mt-4">
            <thead>
                <tr>
                    <th width="130"></th>
                    <th></th>
                    <th></th>
                    <th width="120"></th>
                    <th width="120"></th>
                    <th width="130"></th>
                </tr>
            </thead>
            <tbody>
                {orders && orders.map(item => (
                    <tr key={item.id}>
                        <td>{item.create_at}</td>
                        <td>{item.user.name}</td>
                        <td>
                            <ul>
                                {Object.values(item.products).map(prod => (
                                    <li key={prod.id}>
                                        {prod.product.title}
                                        { prod.qty }{ prod.product.unit }
                                    </li>
                                ))
                                }
                            </ul>
                        </td>
                        <td>{item.total}</td>
                        <td>
                            {item.is_paid == true ? <span className="ispaid"></span> : <span className="unpaid"></span>}
                        </td>
                        <td>
                            <button className="btn btn-open btn-sm" onClick={() => {passData(item); handleToggle(true);}}></button>
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
        {orders && <Pagination pagination={pagination} setNowPage={setNowPage} nowPage={nowPage}></Pagination>}
        <Modal show={show}>
            <Modal.Header closeButton>
                <Modal.Title></Modal.Title>
            </Modal.Header>
            {data.create_at && 
            <Modal.Body>
                <div className='my-2'>{data.user.name}</div>
                <div className='my-2'>{data.id}</div>
                <div className='my-2'>{data.create_at}</div>
                {data.is_paid ? <div className='my-2'></div> : <div className='my-2'></div>}
                <div className='my-2'>Email{data.user.email}</div>
                <div className='my-2'>{data.user.address}</div>
                <table className="table mt-4">
                <thead>
                    <tr>
                        <th width="130"></th>
                        <th width="80"></th>
                        <th width="100"></th>
                        <th width="110"></th>
                        <th width="110"></th>
                        <th width="110"></th>
                    </tr>
                </thead>
                <tbody>
                    {data.products && Object.values(data.products).map((item) => (
                        <tr key={item.id}>
                            <td>{item.product.title}</td>
                            <td>{item.qty}</td>
                            <td>NT{item.product.price}</td>
                            <td>NT{item.total}</td>
                            {item.hasOwnProperty('coupon') ? <td>{item.coupon.percent}%</td> : <td></td>}
                        </tr>
                    ))}
                    <tr>
                        <td colSpan='5'></td>
                        <td>NT{data.total}</td>
                    </tr>
                </tbody>
                </table>
            </Modal.Body>}
            
            <Modal.Footer>
                <Button variant="secondary" onClick={() => handleToggle(false)}>
                
                </Button>
            </Modal.Footer>
        </Modal>
    </>
  )
}